import { NgModule } from '@angular/core';
import { CommonModule } from '@angular/common';
import { Routes, RouterModule } from '@angular/router';

import { IonicModule } from '@ionic/angular';

import { TestePage } from './teste.page';
import { FichasModule } from '../components/ficha/fichas.module';
import { CriacoesModule } from '../components/criacoes/criacoes.module';
import { PaginacaoModule } from '../components/utils/paginacao/paginacao.module';
import { ListasModule } from '../components/utils/listas/listas.module';
import { NovaMesaModule } from '../components/criacoes/nova-mesa.module';
import { InvitationFormModule } from '../components/criacoes/invitation-form.module';

const routes: Routes = [
  {
    path: '',
    component: TestePage
  }
];

@NgModule({
  imports: [
    CommonModule,
    IonicModule,
    RouterModule.forChild(routes),
    FichasModule,
    CriacoesModule,
    PaginacaoModule,
    ListasModule,
    NovaMesaModule,
    InvitationFormModule
  ],
  declarations: [TestePage]
})
export class TestePageModule { }
